 /* Button */

.r-r-button-min-size {
    min-height: 35px;
    min-width: 90px;
}

.r-r-button-min-size-icon-only {
    min-height: 35px;
    min-width: 35px;
}

.r-r-button {
    margin: 5px;
    display: inline-flex;
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    align-items: center;
    vertical-align: bottom;
    text-align: center;
    overflow: hidden;
    position: relative;
    background-color: #F5F8FA;
    color: #293742;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #A7B6C2;
    text-decoration: none;
}

.r-r-button-icon {
    
}

.r-r-button-rounded-border {
    border-radius: 20px;
}

.r-r-button-raised-border {
    border: 1px solid #A7B6C2;
    box-shadow: 1px 2.5px 5px #A7B6C2;
    -webkit-box-shadow: 1px 2.5px 5px #A7B6C2;
    -moz-box-shadow: 1px 2.5px 5px #A7B6C2;
}

.r-r-button:hover {
    -webkit-filter: brightness(97%);
            filter: brightness(97%);
}

.r-r-button:active {
    box-shadow: inset 0px 0px 400px 110px rgba(0, 0, 0, .1);
}

.r-r-button:focus {
    z-index: 99;
    outline: none;
    box-shadow: 0 0 0 3px rgba(167, 182, 194, .5);
    -webkit-box-shadow: 0 0 0 3px rgba(167, 182, 194, .5);
    -moz-box-shadow: 0 0 0 3px rgba(167, 182, 194, .5);
}

/* ButtonGroup */

.r-r-button-group {
    display: flex;
    padding: 0px;
}

.r-r-button-group > .r-r-button {
    margin: 0px;
    border-radius: 0px;
}

.r-r-button-group > .r-r-button:first-child {
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

.r-r-button-group > .r-r-button:last-child {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

.r-r-button-group.r-r-flex-vertical {
    flex-wrap: wrap;
}

.r-r-button-group.r-r-flex-vertical > .r-r-button {
    border-radius: 0px;
}

.r-r-button-group.r-r-flex-vertical > .r-r-button:first-child {
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.r-r-button-group.r-r-flex-vertical > .r-r-button:last-child {
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}


