/* Transition animation */

.transition-dropdown-enter {
    max-height: 0;
    overflow: hidden;
}

.transition-dropdown-enter-active {
    max-height: 1000px;
    transition: max-height 0.5s ease-in;
}

.transition-dropdown-exit {
