/* The loading Class */ 

.r-r-skeleton {
    border: none !important;
}

.r-r-skeleton > * {
    display: none !important;
}

.r-r-loading { 
    position: relative; 
    color: #e4e4e4 !important;
    background-color: #e4e4e4 !important;
    pointer-events: none;
    overflow: hidden !important;
    -webkit-touch-callout: none; /* iOS Safari */
      -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
          -ms-user-select: none; /* Internet Explorer/Edge */
              user-select: none; /* Non-prefixed version, currently
                                    supported by Chrome, Edge, Opera and Firefox */
} 

.r-r-loading::after { 
    display: block; 
    content: ""; 
    position: absolute; 
    width: 100%; 
    height: 100%; 
    top: 0px; 
    overflow: hidden;
    transform: translateX(-100%); 
                  
    background: linear-gradient(90deg, transparent, 
            rgba(255, 255, 255, 0.5), transparent); 

    -webkit-animation: r-r-loading 1.0s infinite; 

            animation: r-r-loading 1.0s infinite; 
} 

@-webkit-keyframes r-r-loading { 
    100% { 
        transform: translateX(100%); 
    } 
} 

@keyframes r-r-loading { 
    100% { 
        transform: translateX(100%); 
    } 
}

/* End The loading Class */ 

