}

.r-r-inputtext:focus {
    background-color: transparent;
}

.r-r-inputtext-flushed {
    border: none;
    border-bottom: 1px solid #c7c7c7;
    border-radius: 0px;
}

.r-r-inputtext-outlined {
    background-color: transparent;
}

.r-r-inputtext-label {
    display: inline-block;
}

.r-r-inputtext-help-label {
    display: inline-block;
}

.r-r-inputtext-icon-pack {
    position: relative;
    display: inline-flex;
    box-sizing: border-box;
    width: 100%;
}

.r-r-inputtext-left-icon {
    position: absolute;
    align-self: center;
    margin-left: 12px;
    max-height: 18px !important;
    max-width: 25px !important;
    z-index: 101;
}

.r-r-inputtext-right-icon {
    position: absolute;
    align-self: center;
    right: 12px;
    max-height: 18px !important;
    max-width: 25px !important;
    z-index: 101;
}

.r-r-inputtext-left-icon-flushed {
    margin-left: 0px;
}

.r-r-inputtext-right-icon-flushed {
    right: 0px;
}

.r-r-floating-label {
    position: relative;
    margin: 40px 0 20px;
}

.r-r-floating-label > label {
    font-weight: normal;
    position: absolute;
    pointer-events: none;
    left: 8px;
    top: 0%;
    transition: 0.2s ease all;
    -moz-transition: 0.2s ease all;
    -webkit-transition: 0.2s ease all;
}

.r-r-floating-label > .r-r-inputtext-label-flushed {
    left: 0px;
}

.r-r-inputtext:focus ~ .r-r-inputtext-label,
.r-r-inputtext:not(:placeholder-shown) ~ .r-r-inputtext-label {
    top: -32px;
    left: 0px;
}

.r-r-inputtext:focus ~ .r-r-inputtext-label-flushed,
.r-r-inputtext:not(:placeholder-shown) ~ .r-r-inputtext-label-flushed {
    top: -23px;
    left: 0px;
}

